@import url("https://fonts.googleapis.com/css2?family=Neucha&display=swap");

.App {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #002e60;
  font-family: "Neucha", cursive;
}

.heading {
  text-transform: uppercase;
  font-size: 40px;
  margin: 30px 0;
  color: white;
  z-index: 1;
  text-align: center;
}

@media (max-width: 800px) {
  .heading {
    margin: 15px 0;
    font-size: 35px;
  }
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

input[type=checkbox] {
  accent-color: rgb(0, 0, 0);
}

.lazy_load_container {
  color: white;
  margin: 30px 0;
}

.performance_value {
  color: white;
}

.settings_container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  max-width: 600px;
}

.settings_container_action {
display: flex;
flex-direction: column;
align-items: center;
}

label {
  color: white;
  font-size: 20px;
}

.lazy_load_checkbox {
  width: 30px;
  height: 30px;
  border: none;
  transition: 0.2s;
  box-shadow: inset 0 0 5px black;
  border-radius: 50px;
  text-align: center;
}

.input_number_custom {
  width: 70px;
  border-radius: 50px;
  text-align: center;
  padding: 10px 20px;
  font-size: 20px;
  border: none;
  transition: 0.2s;
  box-shadow: inset 0 0 5px black;
}

.input_submit_custom {
  width: 200px;
  height: 45px;
  margin: 20px 12px 30px 12px;
  border-radius: 50px;
  right: 0px;
  border: none;
  font-size: 15px;
  background-color: #c0952f;
  color: white;
  transition: 0.2s all;
  box-shadow: 0 0 10px black;
}
.input_submit_custom__warning {
background-color: red;
}
.input_submit_custom:hover {
  cursor: pointer;
  background-color: #e28438;
}

.input_submit_custom:active {
  transform: scale(0.8);
  box-shadow: 0 0 5px black;
}
